import React from 'react';
import PropTypes from 'prop-types';
import {
  Box,
  Button,
  Paper,
  Checkbox,
  FormControlLabel
} from '@material-ui/core'

import { withStyles } from '@material-ui/core/styles';
import SearchIcon from '@material-ui/icons/Search';
import RefreshIcon from '@material-ui/icons/Refresh';

const styles = (theme) => ({
  paper: {
    maxWidth: 936,
    margin: 'auto',
    overflow: 'hidden',
  },
  searchBar: {
    borderBottom: '1px solid rgba(0, 0, 0, 0.12)',
  },
  searchInput: {
    fontSize: theme.typography.fontSize,
  },
  block: {
    display: 'block',
  },
  addUser: {
    marginRight: theme.spacing(1),
  },
  contentWrapper: {
    margin: '40px 16px',
  },
});

function Export(props) {
  const { classes } = props;

  return (
    <Paper className={classes.paper}>
      <Box mt={3}>
        <Box className="form-div">
            <label className='form-label-addnew'>
            対象ファイル
            </label>
            <div className='form-input-addnew'>
                <select>
                  <option> 1 </option>
                  <option> 2 </option>
                  <option> 3 </option>
                  <option> 4 </option>
                </select>
            </div>
        </Box>
        <Box className="form-div">
            <label className='form-label-addnew'>
            モデル
            </label>
            <div className='form-input-addnew' style={{alignSelf:"center"}}>
              <Box>
                <FormControlLabel
                  control={<Checkbox  name="checkedG" />}
                  label="頭の痛み"
                />
              </Box>
              <Box>
                <FormControlLabel
                  control={<Checkbox  name="checkedG" />}
                  label="胸の痛み"
                />
              </Box>
              <Box>
                <FormControlLabel
                  control={<Checkbox  name="checkedG" />}
                  label="足の震え"
                />
              </Box>
            </div>
        </Box>
        <Box>
          <Button className="import-btn" >エクスポート</Button>
        </Box>
      </Box>
    </Paper>
  );
}

Export.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Export);